@import "./src/assets/sass/prepends.scss";

.card {
  text-align: center;
  margin: 2rem auto;
  padding: 1rem 2rem;

  .icon {
    width: 100%;
    height: 5rem;
    padding: 2rem 0;

    > * {
      width: 40px;
      height: 40px;
      display: block;
      margin: 0 auto;
    }
  }
  .links {
    width: 100%;
    margin: 4rem 0 0;
    &.right {
      text-align: right;
    }
    &.left {
      text-align: left;
    }
  }
}
.text-success {
  color: $text-green;
}
.text-error {
  color: $text-red;
}
