@import "./src/assets/sass/prepends.scss";

.preview-content-wrapper {
  display: inline-block;
  position: relative;
  align-self: center;
  width: 9rem;
  height: 8rem;
  border: 1px solid #aaa;
  border-radius: 0.3rem;
  margin: 0.5rem;
  padding: 0.5rem;
  z-index: 1;
  cursor: pointer;
  background-color: $bg-white;

  .icon-wrapper {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    text-align: center;

    img,
    svg {
      display: block;
      margin: 0 auto;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: scale-down;
    }
    svg {
      width: 4rem;
      height: auto;
    }
  }

  .preview-file-name {
    position: absolute;
    bottom: 0.5rem;
    font-size: 0.6rem;
  }
}
